<template>
    <div class="detail">
            <van-nav-bar
                title="详情"
                left-text="返回"
                @click-left="back"
                left-arrow
            />
             <!-- 商品内容 -->
            <div class="detail_content">
                <div class="detail_img">
                     <img class="auto-img"  :src="productData.large_img"/>
                </div>


                 <div class="clearfix detail-info">
                    <span class="fl pro-name">{{productData.name}}</span>
                    <span class="fr like" @click="likeAndNotLike">
                       <img class="auto-img" :src="require('../assets/images/' + (isLike ? 'like_active.png' : 'like.png'))"  alt=""/>
                    </span>
                </div>

                 <!-- 商品描述 -->
                <div class="desc-box">
                    <div class="desc-title">商品描述</div>
                    <div class="desc-content">
                        <div class="desc-item" v-for="(item, index) in productData.desc" :key="index">{{item}}</div>
                    </div>
                </div>

                <!-- 商品规格 -->
                <div class="rule-box">
                    <div class="rule-box_title">商品规格</div>
                    <div class="rule-item clearfix"  v-for="(item, index) in productData.rules" :key="index">
                        <div class="fl rule-title">{{item.title}}</div>
                        <div class="rule-tags clearfix fl">
                             <div class="fl tag-item" :class="{active: v.isActive}" v-for="(v, i) in item.tag" :key="i" @click="toggleRuleTag(item, v)">{{v.name}}</div>
                        </div>
                    </div>

                   
                    <div class="clearfix price-box">
                        <div class="fl price">￥{{productData.price}}</div>
                        <div class="fr">
                        <van-stepper v-model="value" :integer="true" theme="round" button-size="22" disable-input />
                        </div>
                    </div>

                </div>
           </div>
           <van-goods-action>
                <van-goods-action-icon @click="goShopcart" icon="cart-o" text="购物车"  :badge="shopcartRows == 0 ? '' : shopcartRows"/>
                <van-goods-action-button type="warning" text="加入购物车" @click="addShopcart"/>
                <van-goods-action-button type="danger" text="立即购买" @click="addShopcart(true)"/>
            </van-goods-action>
    </div>
</template>

<script>
import '../assets/css/detail.less'
    export default {
        data(){
            return {
              
                value: 1,
             
                pid: '',
               
                productData: {},
                isLike: false,

                shopcartRows: 0
            }
        },
        created(){
            
            this.pid = this.$route.query.pid;

            this.getProductDetail(this.pid);

            this.findLike(this.pid);

            this.findShopcart();
        },
        methods: {
            //根据商品id获取商品详情数据
            getProductDetail(pid){
                
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    duration: 0
                });

                this.axios({
                    methods: 'GET',
                    url: '/productDetail',
                    params: {
                        pid,
                        appkey: this.appkey
                    }
                }).then(result =>{
                    this.$toast.clear();
                     
                     if(result.data.code ==600){
                         let data= result.data.result[0];
                         data.desc=data.desc.split(/\n/);

                         //商品规格
                         let productRule = [{name:'tem'},{name: 'sugar'}, {name:'milk'}, {name:'cream'}];
                         

                          let rules= [];
                          productRule.forEach(v=>{
                              if(data[v.name]!=''){
                                  let rule= {
                                      title: data[v.name+'_desc'],
                                      tag: []
                                  };
                                  let tag = data[v.name].split(/\//);

                                  tag.forEach((item, i) => {
                                    let ttrule = {
                                        name: item,
                                        isActive: i == 0
                                    };
                                        rule.tag.push(ttrule);
                                    })
                                    rules.push(rule);
                              }
                          })

                        

                        data.rules=rules;

                        this.productData = data;
                        

                     }
                }).catch(err =>{
                     this.$toast.clear();
                     
                })
            },

            //切换规格标签
            toggleRuleTag(item,v){
                if(v.isActive){
                    return;
                }

                 for (let i = 0; i < item.tag.length; i++) {
                    if (item.tag[i].isActive) {
                        item.tag[i].isActive = false;
                        break;
                    }
                }
                v.isActive = true;
            },
         
            likeAndNotLike(){
                 
                let tokenString = localStorage.getItem('__Tk');

                let url = this.isLike ? '/notlike' : '/like';

                //开启加载提示
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    duration: 0
                });

                this.axios({
                    method: 'POST',
                    url,
                    data: {
                        appkey: this.appkey,
                        pid: this.pid,
                        tokenString
                    }
                }).then(result =>{
                    this.$toast.clear();
                    
                    if (result.data.code == 800 || result.data.code == 900) {
                       
                        this.isLike = result.data.code == 800;
                        this.$toast(result.data.msg);
                    } else {
                        
                        this.$router.push({name: 'Login'});
                    }
                }).catch(err =>{
                    this.$toast.clear();
                    
                })
            },
           
             findLike(pid){
             
                let tokenString = localStorage.getItem('__Tk');

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    duration: 0
                });

                this.axios({
                    method: 'GET',
                    url: '/findlike',
                    params: {
                        appkey: this.appkey,
                        pid,
                        tokenString
                    }
                }).then(result => {
                    this.$toast.clear();
                    

                    if (result.data.code == 1000) {

                    this.isLike = result.data.result.length > 0;

                    } else {
                   
                    this.$router.push({name: 'Login'});
                    }
                    
                }).catch(err => {
                    this.$toast.clear();
                    
                })
             },
            //  返回
             back(){
                 this.$router.go(-1);
             },
             //查询购物车商品条数
             findShopcart(){
                
                 let tokenString = localStorage.getItem('__Tk');

                 this.$toast.loading({
                    message: '加载中',
                    forbidClick: true,
                    duration: 0
                 })

              
                 this.axios({
                    method: 'GET',
                    url: '/findAllShopcart',
                    params: {
                    appkey: this.appkey,
                    tokenString
                    }
                }).then(result => {
                    this.$toast.clear();
                    

                    if (result.data.code == 5000) {
                    this.shopcartRows = result.data.result.length;
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })
             },

            //  加入购物车
            addShopcart(flag){
                //获取token
                let tokenString = localStorage.getItem('__Tk');
                //获取商品规格
                let rules = this.productData.rules;
                
                let rule = [];
                rules.forEach(v => {
                    for (let i = 0; i < v.tag.length; i++) {
                        if (v.tag[i].isActive) {
                            rule.push(v.tag[i].name);
                            break;
                        }
                    }
                })

                

                this.$toast.loading({
                     message: '加载中',
                    forbidClick: true,
                    duration: 0
                })

                this.axios({
                    method: 'POST',
                    url: '/addShopcart',
                    data: {
                        appkey: this.appkey,
                        tokenString,

                        pid: this.pid,
                      
                        count: this.value,
            
                        rule: rule.join('/')
                    }
                }).then(result => {
                    this.$toast.clear();
                    
                    if (result.data.code == 3000) {

                        if (result.data.status == 1) {
                            this.shopcartRows++;
                        }
                        
                        this.$toast(result.data.msg);
                          if (flag === true) {
                            setTimeout(() => {
                            this.$router.push({name: 'Pay', query: {sids: result.data.sid}});
                            }, 400)
                        }
                    }else{
                        this.$toast(result.data.msg);
                    }
                }).catch(err => {
                    this.$toast.clear();
                    
                })

            },
             //跳转到购物车
            goShopcart() {
              this.$router.push({name: 'Shopcart'});
            }
        }
    }
</script>
